@import "../../../styles/theme.scss";
.text-area {
  width: 100%;
  height: 100%;
  border-radius: 4px;
  position: relative;
  border-bottom: $border-grey 1px solid;
  transition: 0.3s all;
  &:hover {
    background-color: rgba(228, 228, 228, 0.45);
  }
  textarea {
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    position: relative;
    padding: 0px 16px;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    font-weight: 400;
    line-height: normal;
    background-color: transparent;
    color: #282828;
    outline: none;
    resize: none;
    box-shadow: 0px 4px 20px 0px transparent;
    transition: 0.3s background-color ease-in-out;
  }
}
